<!-- Card.svelte -->
<script>
  import { gsap } from "gsap";
  export let id = "";
  export let imageUrl = "";
  export let url = "";
  const Card_Animation = gsap.timeline();

  const Animate_Card_On_Hover = () => {
    const cardSelector = `#card-${id}`;

    Card_Animation.timeScale(1).play();
    setTimeout(() => {
      Card_Animation.to(cardSelector, {
        scale: 1.05,
        borderColor: "#fafafa35"
      }).to(`${cardSelector} .Card-Thumbnail`, {
        backgroundPositionY: 400,
        duration: 20,
        repeat: -1
      });
    }, -1);
  };
</script>

<div class="Card bg-gray-900/50 overflow-hidden relative border-2 border-gray-900 w-full h-60 rounded-lg flex flex-col cursor-pointer" on:mouseover={Animate_Card_On_Hover} on:mouseout={() => {Card_Animation.timeScale(20).reverse()}} id={`card-${id}`} on:click={() => {window.open(`${url}`, '_blank')} }>
  <div id="thumb"class="Card-Thumbnail p-5 w-full h-40 bg-cover" style={`background-image: url('${imageUrl}')`}></div>
  <div class="Card-Info w-full h-20 rounded-b-lg flex flex-col justify-center items-start px-2">
    <h2 class="text-white font-['Roboto_Mono'] text-xl my-1">
      <slot name="Card-name"></slot><span class="text-lg mx-2 text-gray-500">
        <slot name="Card-release"></slot>
      </span>
    </h2>
    <div class="flex flex-row items-start justify-center w-full">
      <span class="text-white font-['Roboto_Mono'] text-sm bg-blue-500/50 rounded-full w-full border-2 border-blue-500 mx-1">
        <slot name="Card-Tag-1"></slot>
      </span>
      <span class="text-white font-['Roboto_Mono'] text-sm bg-orange-500/50 rounded-full w-full border-2 border-orange-500 mx-1">
        <slot name="Card-Tag-2"></slot>
      </span>
      <span class="text-white font-['Roboto_Mono'] text-sm bg-green-500/50 rounded-full w-full border-2 border-green-500 mx-1">
        <slot name="Card-Tag-3"></slot>
      </span>
      <span class="text-white font-['Roboto_Mono'] text-sm bg-purple-500/50 rounded-full w-full border-2 border-purple-500 mx-1">
        <slot name="Card-Tag-4"></slot>
      </span>
    </div>
  </div>
</div>
